.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 10;
}

.header-bg {
  background-color: rgba(0, 0, 0, 0.85);
  transition: 0.5s;
}

.nav-menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  position: absolute;
  top: 0;
  left: -100%;
  z-index: -3;
  transition: 0.3s;
}

.nav-menu.active {
  left: 0;
}

.nav-menu li {
  padding: 1rem 0;
}
.nav-menu li a {
  font-size: 2rem;
  font-weight: 500;
}

@media only screen and (min-width: 768px) {
  .nav-menu {
    flex-direction: row;
    left: 0;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    background: none;
  }
  .hamburger {
    display: none;
  }
  .nav-menu li {
    padding: 0 1rem;
  }

  .nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
    transition: all 0.3s ease 0s;
  }

  .nav-menu li a:hover {
    color: rgb(248, 217, 15);
  }
}
